.wrap {
    max-width: 792px + 80px;
    margin: 80px auto 136px;
    padding: 0 40px;

    @media screen and (max-width: 768px) {
        flex-direction: column;
        margin: 30px auto;
        padding: 0;
    }

    .main {
        display: flex;
        justify-content: space-between;
        padding: 40px 0 0;

        @media screen and (max-width: 768px) {
            flex-direction: column;
            padding: 30px 16px 0
        }

        .left {
            width: 332px;

            @media screen and (max-width: 768px) {
                width: auto;
                margin: 0 0 20px;
                overflow: scroll;
                display: flex;
            }

            .item {
                display: flex;
                align-items: center;
                background: #181818;
                border: 1px solid rgba(0, 0, 0, 0.08);
                box-shadow: 0px 8px 32px rgba(138, 11, 11, 0.05);
                border-radius: 16px;
                margin: 0 0 22px;
                box-sizing: border-box;
                padding: 26px 24px;

                @media screen and (max-width: 768px) {
                    flex: 0 0 calc(50% - 30px);
                    padding: 10px;
                    margin: 0 10px 10px 0;
                    border-radius: 8px;

                    &:last-child {
                        margin: 0 0 10px 0;
                    }
                }


                .info {
                    margin: 0 0 0 16px;

                    .symbol {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 20px;
                        color: rgba(#fff, .6);

                        @media screen and (max-width: 768px) {
                            font-family: 'Poppins';
                            font-style: normal;
                            font-weight: 400;
                            font-size: 12px;
                            line-height: 18px;
                        }
                    }

                    .num {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 24px;
                        line-height: 30px;
                        color: #fff;
                        margin: 4px 0 0;

                        @media screen and (max-width: 768px) {
                            font-family: 'Poppins';
                            font-style: normal;
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 20px;
                        }
                    }
                }
            }
        }

        .right {
            margin: 0 0 0 40px;

            @media screen and (max-width: 768px) {
                margin: 0;
            }

            .multipleMintHeader {
                display: flex;
                align-items: center;
                padding: 0 0 20px;

                .text {
                    flex: 1;
                    text-align: center;
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 24px;
                    text-align: center;
                    color: #FFFFFF;
                }
            }

            .token{
                display: flex;
                align-items: center;
                img{
                    height: 24px;
                    width: 24px;
                }
                span{
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 20px;
                    margin: 0 0 0 6px;
                }
            }

            .transferBox {
                position: relative;
                height: 4px;

                .transferBtn {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    border: 1px solid #373737;
                    background: #181818;
                    height: 30px;
                    width: 30px;
                    padding: 0;

                    .transferIcon {
                        fill: rgba(#fff, .6);
                        transition: all 0.3s;
                        height: 30px;
                        width: 30px;

                        &:hover {
                            fill: rgba(255, 255, 255, 1);
                        }
                    }
                }
            }

            .feeBox{
                padding: 20px 0;
                .fee{
                    display: flex;
                    justify-content: space-between;
    
                    .txt{
                        display: flex;
                        align-items: center;
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 600;
                        font-size: 14px;
                        line-height: 22px;
                        color: rgba(#fff, .6);
                        span{
                            margin: 0 10px 0 0;
                        }
                    }
                }

                .feeNumber{
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 24px;
                    color: rgba(#fff, .3);
                }
            }

        }
    }
}