.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;

    .box {
        background: #181818;
        border: 1px solid rgba(0, 0, 0, 0.08);
        border-radius: 20px;
        padding: 20px;
        width: 450px;

        @media screen and (max-width: 768px) {
            max-width: 390px;
            margin: 0 10px;
        }

        .title {
            display: flex;
            justify-content: space-between;
            padding: 0 0 20px;
            align-items: center;

            .txt {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 30px;
                color: #FFFFFF;
            }

            button {
                margin: -10px -10px 0 0;

                .icon {
                    width: 24px;
                    height: 24px;
                }
            }
        }

        .list {
            background: #181818;
            border: 1px solid #373737;
            border-radius: 12px;
            padding: 0 16px;
            position: relative;

            &:first-child {
                margin: 0 0 4px;
            }

            .down {
                width: 30px;
                height: 30px;
                border-radius: 30px;
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                top: -17px;
                left: 50%;
                background: #181818;
                border: 1px solid #373737;
                transform: translateX(-50%);
            }

            .item {
                height: 50px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-bottom: 1px solid #373737;

                &:last-child {
                    border-bottom: 0 none;
                }

                .token {
                    display: flex;
                    align-items: center;

                    .txt {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 600;
                        font-size: 20px;
                        line-height: 24px;
                        color: #FFFFFF;
                        margin: 0 0 0 8px;
                    }
                }

                .value {
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 24px;
                    line-height: 24px;
                    text-align: right;
                    letter-spacing: 0.48px;
                    color: #FFFFFF;

                }
            }
        }

        .info{
            padding: 30px 0;

            .item{
                display: flex;
                justify-content: space-between;
                margin: 0 0 6px;

                &:last-child{
                    margin: 0;
                }

                .txt{
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 24px;
                    color: rgba(#fff, .6);
                }

                .value{
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 24px;
                    text-align: right;
                    color: #FFFFFF;
                }
            }
        }


        .btnWrap {
            display: flex;

            .confirm {
                flex: 1;
                height: 56px;
                background: linear-gradient(243.59deg, #71FF3A 14.24%, #00D3BA 83.41%);
                border-radius: 12px;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                color: #000;
                transition: all 0.3s;

                &:hover {
                    opacity: 0.7;
                }
            }

        }
    }
}