.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;

    .box {
        background: #181818;
        border: 1px solid rgba(0, 0, 0, 0.08);
        border-radius: 20px;
        padding: 20px;
        width: 450px;

        @media screen and (max-width: 768px) {
            max-width: 390px;
            margin: 0 10px;
        }

        .title {
            display: flex;
            justify-content: space-between;
            padding: 0 0 20px;
            align-items: center;

            .txt {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 30px;
                color: #FFFFFF;
            }

            button {
                margin: -10px -10px 0 0;

                .icon {
                    width: 24px;
                    height: 24px;
                }
            }
        }

        .block {
            background: #181818;
            border: 1px solid #373737;
            border-radius: 12px;
            padding: 12px 16px;

            .info {
                display: flex;
                justify-content: space-between;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 22px;

                .txt {
                    color: rgba(255, 255, 255, 0.4);
                }

                .num {
                    color: rgba(255, 255, 255, 0.4);
                }
            }

            .form {
                display: flex;
                align-items: center;
                margin: 16px 0 0;

                .input {
                    flex: 1;

                    :global(.MuiOutlinedInput-input) {
                        border: 0 none;
                        padding: 0;
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 24px;
                        line-height: 24px;
                        letter-spacing: 0.48px;
                        color: rgba(255, 255, 255, 0.6);
                    }

                    :global(.MuiOutlinedInput-notchedOutline) {
                        display: none;
                    }
                }

                .btn {
                    background: #22301f;
                    border: 1px solid #22301f;
                    border-radius: 8px;
                    height: 24px;
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 14px;
                    transition: all 0.3s;
                    padding: 0 6px;
                    min-width: auto;
                    margin: 0 10px;

                    &:hover {
                        border: 1px solid rgba(113, 255, 58, 0.5);
                    }
                }

                .txt {
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 20px;
                    line-height: 24px;
                    text-align: center;
                    color: #FFFFFF;
                }

            }
        }

        .btnWrap {
            display: flex;
            padding: 110px 0 20px;

            .confirm {
                flex: 1;
                height: 56px;
                background: linear-gradient(243.59deg, #71FF3A 14.24%, #00D3BA 83.41%);
                border-radius: 12px;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                color: #000;
                transition: all 0.3s;

                &:hover {
                    opacity: 0.7;
                }
            }

            .cancel {
                flex: 1;
                border: 1px solid #71FF3A;
                border-radius: 12px;
                height: 56px;
                margin: 0 20px 0 0;
                padding: 0;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 600;
            }
        }

        .link {
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            transition: all 0.3s;
            text-decoration: none;

            &:hover {
                opacity: 0.7;
            }

            span {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 20px;
                color: #71FF3A;
                margin: 0 10px 0 0;
            }

            svg {
                fill: #71FF3A;
            }
        }

    }
}