@font-face{
  font-family: 'Poppins';
  src: url('assets/fonts/Poppins-Regular.ttf') format('truetype');
  font-weight: 400;
}

@font-face{
  font-family: 'Poppins';
  src: url('assets/fonts/Poppins-Medium.ttf') format('truetype');
  font-weight: 600;
}

@font-face{
font-family: 'Poppins';
  src: url('assets/fonts/Poppins-Bold.ttf') format('truetype');
  font-weight: 800;
}

::-webkit-scrollbar{
  display: none;
  scrollbar-width: none;
}

.MuiMenu-paper{
  border-radius: 12px !important;
  border: 1px solid rgba(113, 255, 58, 0.2);
  margin-top: 5px;
  .MuiMenu-list{
    padding: 8px;

    .MuiMenuItem-root{
      border-radius: 6px;
      height: 40px;
      .MuiTypography-root{
        font-size: 16px;
        align-items: center;
        .MuiAvatar-root{
          margin: 0 6px 0 0;
          height: 16px;
          width: 16px;
        }
      }
      
    }

    .Mui-selected {
      background:  rgba(#fff, 0.1);
    }
  }
}