.snackbar {
    :global(.MuiPaper-root) {
        background: #181818;
        border: 1px solid #373737;
        border-radius: 16px;
        padding: 10px 22px;
    }

    :global(.MuiSnackbarContent-message) {
        display: none;
    }

    :global(.MuiSnackbarContent-action) {
        padding: 0;
        margin: 0;
        display: flex;
        width: 100%;
    }

    .main {
        position: relative;
        width: 100%;
        padding: 0 20px 0 20px;
        box-sizing: border-box;

        .title {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 28px;
            letter-spacing: 0.01em;
            color: #FFFFFF;
        }

        .message {
            a {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 28px;
                letter-spacing: 0.01em;
                color: rgba(#fff, .6);
                text-decoration: none;
                display: flex;
                align-items: center;
            }

            svg {
                fill: rgba(#fff, .6);
                margin: 0 0 0 10px;
                width: 12px;
                height: 12px;
            }
        }

        .close {
            position: absolute;
            right: -15px;
            top: -2px;

            .icon {
                width: 12px;
                height: 12px;
            }
        }

        .successIcon, .failureIcon{
            position: absolute;
            top: 5px;
            left: -10px
        }
    }
}