.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;

    .box {
        background: #181818;
        border: 1px solid rgba(0, 0, 0, 0.08);
        border-radius: 20px;
        padding: 20px;
        width: 450px;

        @media screen and (max-width: 768px) {
            max-width: 390px;
            margin: 0 10px;
        }

        .title {
            display: flex;
            justify-content: space-between;
            padding: 0 0 20px;
            align-items: center;

            .txt {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 30px;
                color: #FFFFFF;
            }

            button {
                margin: -10px -10px 0 0;

                .icon {
                    width: 24px;
                    height: 24px;
                }
            }
        }

        .main {
            text-align: center;
            padding: 60px 0;

            .loading {
                margin: 0 0 40px;
            }

            .message {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 20px;
                color: rgba(#fff, .6)
            }

            .detail {
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 20px;
                color: rgba(#fff, .3);
                margin: 10px 0 0
            }
        }
    }
}